import { Icon as LegacyIcon } from "@ant-design/compatible";
import { Card, Col, Row } from "antd";

const CEditAssetImage = ({
  image,
  handlePickImage,
  imageUrl,
  loading,
  isImageDua,
  handlePickImage2,
  imageUrlDua,
  loadingDua,
  onPickImage,
  onPickImage2,
}) => {
  return (
    <Row span={24}>
      <input
        type="file"
        id="select-image"
        accept="*"
        onChange={(e) => onPickImage(e.target.files[0])}
        style={{ display: "none" }}
      />
      <input
        type="file"
        id="select-image-2"
        accept="image/*"
        onChange={(e) => onPickImage2(e.target.files[0])}
        style={{ display: "none" }}
      />
      <Col span={12}>
        <Col
          style={{
            textAlign: "center",
            fontWeight: "bold",
            marginBottom: 5,
          }}
        >
          Original Photo
        </Col>
        <Col
          span={24}
          style={{
            display: "flex",
            justifyContent: "center",
            justifyItems: "center",
            alignItems: "center",
            marginBottom: 10,
          }}
        >
          {image ? (
            <Card
              hoverable
              style={{
                borderStyle: "none",
              }}
              onClick={handlePickImage}
            >
              <img src={imageUrl} style={{ width: "100%" }} />
            </Card>
          ) : (
            <Card
              style={{
                backgroundColor: "#d3d3d3",
                height: 120,
                width: 120,
                textAlign: "center",
                display: "flex",
                justifyContent: "center",
                justifyItems: "center",
                alignItems: "center",
                fontSize: 30,
                borderStyle: "dotted",
                borderRadius: 5,
              }}
              hoverable
              onClick={handlePickImage}
            >
              <LegacyIcon type={loading ? "loading" : "plus"} />
            </Card>
          )}
        </Col>
      </Col>
      <Col span={12}>
        <Col
          style={{
            textAlign: "center",
            fontWeight: "bold",
            marginBottom: 5,
          }}
        >
          Last Photo
        </Col>
        <Col
          span={24}
          style={{
            display: "flex",
            justifyContent: "center",
            justifyItems: "center",
            alignItems: "center",
            marginBottom: 10,
          }}
        >
          {isImageDua ? (
            <Card
              hoverable
              style={{
                borderStyle: "none",
              }}
              onClick={handlePickImage2}
            >
              <img src={imageUrlDua} style={{ width: "100%" }} />
            </Card>
          ) : (
            <Card
              style={{
                backgroundColor: "#d3d3d3",
                height: 120,
                width: 120,
                textAlign: "center",
                display: "flex",
                justifyContent: "center",
                justifyItems: "center",
                alignItems: "center",
                fontSize: 30,
                borderStyle: "dotted",
                borderRadius: 5,
              }}
              hoverable
              onClick={handlePickImage2}
            >
              <LegacyIcon type={loadingDua ? "loading" : "plus"} />
            </Card>
          )}
        </Col>
      </Col>
    </Row>
  );
};

export default CEditAssetImage;
